
import { TiLocale } from '@cloud/tiny3';
import { defaulten_US, defaultes_ES, defaultpt_BR } from '@cloud/tinyplus3/locale/i18n';

export class TpConfig {
    public static init(language: string = 'en-US'): void {
        const words: any = TiLocale.getWords();
        // 设置所有语言包
        TiLocale.setWords({
            'en-US': { ...words['en-US'], ...defaulten_US },
            'es-ES': { ...words['es-ES'], ...defaultes_ES },
            'pt-BR': { ...words['pt-BR'], ...defaultpt_BR }
        });
        // 设置默认语言
        TiLocale.setLocale(language);
    }
}
